<template>
  <header class="header">
    <div class="top_cont">
      <div class="top">

<!--  ######   LOGO  ######  -->
        <div class="logo1" style="float: left">
          <router-link to="/">
            <img class="img_logo1" alt="logo" src="../../assets/img/logo1.png" height="80" />
          </router-link>
        </div>

        <div class="logo2" style="float: left">
          <img class="img_logo2" alt="logo" src="../../assets/img/logo2.png" height="80" />
        </div>

<!--  ######   BUSCADOR  ######  -->
        <div class="con_buscador">
          <label for="buscador">
              <input id="buscador" v-on:keyup="buscar"
              class="buscador"
              type="text"
              placeholder="Ej: Coches"
              />
          </label>
        </div>

<!--  ######   ICONOS  ######  -->
        <div class="account" v-on:mouseover="mouseover_carro" style="cursor:pointer">
          <svg xmlns="http://www.w3.org/2000/svg" height="40px" viewBox="0 0 24 24" width="40px" fill="#7e71b4"><path d="M0 0h24v24H0V0z" fill="none"/><path d="M15.55 13c.75 0 1.41-.41 1.75-1.03l3.58-6.49c.37-.66-.11-1.48-.87-1.48H5.21l-.94-2H1v2h2l3.6 7.59-1.35 2.44C4.52 15.37 5.48 17 7 17h12v-2H7l1.1-2h7.45zM6.16 6h12.15l-2.76 5H8.53L6.16 6zM7 18c-1.1 0-1.99.9-1.99 2S5.9 22 7 22s2-.9 2-2-.9-2-2-2zm10 0c-1.1 0-1.99.9-1.99 2s.89 2 1.99 2 2-.9 2-2-.9-2-2-2z"/></svg>
        </div>
        <div class="account" v-on:mouseover="mouseover" style="cursor:pointer">
          <svg xmlns="http://www.w3.org/2000/svg" height="40px" viewBox="0 0 24 24" width="40px" fill="#7e71b4"><path d="M0 0h24v24H0z" fill="none"/><path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm0 3c1.66 0 3 1.34 3 3s-1.34 3-3 3-3-1.34-3-3 1.34-3 3-3zm0 14.2c-2.5 0-4.71-1.28-6-3.22.03-1.99 4-3.08 6-3.08 1.99 0 5.97 1.09 6 3.08-1.29 1.94-3.5 3.22-6 3.22z"/></svg>
        </div>

<!--  ######   NAV MENU  ######  -->
        <div class="menu">
            <div>
              <nav>
                <router-link to="/">CATEGORIAS</router-link>
                <router-link to="/about"> OFERTAS</router-link>
              </nav>
            </div>
         </div>
        <div class="shadow"></div>

<!--  ######   MENU USUARIO  ######  -->
        <div id="user_options" class="user_options" v-on:mouseout="mouseout" v-on:mouseover="mouseover">
          <div class="shadow1"></div>
          <div style="width:100%; height:2px;background-color:#ccc"></div>
          <div class="user_menu">Mis Compras</div>
          <div style="width:100%; height:2px;background-color:#ccc"></div>
          <div class="user_menu">Mi Perfil</div>
          <div style="width:100%; height:2px;background-color:#ccc"></div>
          <div class="user_menu">Salir</div>
          <div style="width:100%; height:2px;background-color:#ccc"></div>
          <div style="position:absolute;width:100%; height:18px;background-color:#7e71b4;bottom:0px;"></div>
        </div>

<!--  ######   MENU CARRO  ######  -->
        <div id="cart_options" class="cart_options" v-on:mouseout="mouseout_carro" v-on:mouseover="mouseover_carro">
          <div class="shadow1"></div>
          <div style="width:100%; height:2px;background-color:#ccc"></div>
          <div class="user_menu">Mi Carro</div>
          <div style="width:100%; height:2px;background-color:#ccc"></div>
          
          <table border="1">
            <tr v-for="(value, propertyName, index) in this.$root.carro" :key="index">
              <td class="cart_item" style="white-space: normal;">{{ value.description }}</td>
              <td class="cart_item" style="text-align:right; vertical-align: bottom">$ {{ value.price.toLocaleString().replace(/,/g, '.') }}</td>
            </tr>
          </table>

          <div class="user_menu" style="padding-top:5px; border:1px solid red; height: 25px;"><b>DESPACHO:</b></div>
          <div class="user_menu" style="padding-top:5px;"><b>TOTAL:</b></div>

          <div style="position:absolute;width:100%; height:18px;background-color:#7e71b4;bottom:0px;"></div>
        </div>      


      </div>
    </div>

    
  </header>
</template>

<script>
import router from '../../routes/routes'
import { reactive, toRefs, computed} from '@vue/reactivity'

export default {
  name: "HomeTop",
  props: {
    msg: String
  },
  setup(){
      const state = reactive({
        text: "",
        cart_items: computed(() => get_cart()),
      })

      function get_cart(){
        return []
      }
      return {... toRefs(state)}
  },
  methods: {
    mouseover: function(){
      document.getElementById('cart_options').style.display='none';
      document.getElementById('user_options').style.display='block';
    },    
    mouseout: function(){
        document.getElementById('user_options').style.display='none'
    },
    mouseover_carro: function(){
      document.getElementById('user_options').style.display='none'
      document.getElementById('cart_options').style.display='block';
    },    
    mouseout_carro: function(){
        document.getElementById('cart_options').style.display='none'
    },
    buscar: function(){
      let products = JSON.parse(localStorage.getItem('products'));
      let txt = document.getElementById('buscador').value.trim()
      if(txt.trim()==""){
        router.push({ path:'/' },{products: products, buscador: ""});
      }
      if(txt.trim().length>2){
        let products = JSON.parse(localStorage.getItem('products'));
        let res = true
        let res1 = false
        for(let v=0;v<txt.length;v++){
            if(txt[v]==txt[(txt.length-1)-v]){res1=true}else{res1=false;res=res1}
        }
        if(res){
          console.log('POLINDRMO')
        }
        
        var filter_products = products.filter(
          item => item.brand.toLowerCase().includes(txt.toLowerCase())  || 
                  item.description.toLowerCase().includes(txt) 
        );
        router.push({ path:'/filter/'+txt },{products: filter_products, buscador: txt});
      }else{
        let products = JSON.parse(localStorage.getItem('products'));
        router.push({ path:'/' },{products: products, buscador: ""});
      }
    }
  }
};


</script>

<style>
@import "../..//assets/css/app.css";
</style>